.contact{background:linear-gradient(to bottom,#191B1B,#0d0d0d);padding:100px 20px;display:flex;flex-direction:column;align-items:center;width:100%;text-align:center;position:relative;min-height:75vh;justify-content:center;}.contact-content{display:flex;flex-direction:column;align-items:center;width:100%;max-width:1200px;margin-bottom:auto;justify-content:center;}.contact-left{display:flex;flex-direction:column;justify-content:center;align-items:center;max-width:100%;}.contact-left h2{font-family:'Instrument Sans',sans-serif;font-size:60px;font-weight:700;margin:0;color:#FFFFFF;}.contact-left p{font-size:24px;color:#b3b3b3;margin:20px 0;}.social-icons{display:flex;gap:20px;margin-top:20px;justify-content:center;}.social-icons a{color:#FFFFFF;font-size:32px;transition:transform 0.3s ease;}.social-icons a:hover{transform:scale(1.1);color:#D3E87D;}.contact-right{display:flex;align-items:center;margin-top:20px;}.get-in-touch{background-color:#FFFFFF;color:#0d0d0d;border:none;padding:15px 30px;border-radius:50px;font-size:18px;cursor:pointer;display:flex;align-items:center;gap:10px;transition:background-color 0.3s ease;text-decoration:none;}.get-in-touch:hover{background-color:#D3E87D;}.footer-nav-wrapper{display:flex;justify-content:center;align-items:center;width:100%;padding:10px 0;position:absolute;bottom:0;left:0;text-align:center;}.footer-nav-wrapper p{color:#b3b3b3;font-size:16px;margin:0;}.footer-nav-wrapper p:hover{color:#D3E87D;}@media(max-width:768px){.contact{padding:0px 20px;text-align:center;min-height:auto;justify-content:center;background:none;}.contact-content{flex-direction:column;align-items:center;justify-content:center;}.contact-left{max-width:100%;margin-bottom:20px;}.contact-left h2{font-size:35px;}.contact-left p{font-size:16px;margin:10px 0;}.social-icons{justify-content:center;gap:15px;}.social-icons a{font-size:24px;}.contact-right{margin-top:20px;}.get-in-touch{padding:10px 20px;font-size:16px;border-radius:40px;}.footer-nav-wrapper{padding:10px 0;position:static;margin-top:20px;}}
